import { createRouter, createWebHashHistory } from 'vue-router'
import service_index from '../components/service/index.vue'
import service_infor from '../components/service/infor.vue'
import service_type from '../components/service/type.vue'
import crowdsourcing_index from '../components/crowdsourcing/index.vue'
const routes = [
  {
    path: '/',
    name: 'service_index',
    component: service_index
  },
  {
    path: '/service_infor',
    name: 'service_infor',
    component: service_infor
  },
  {
    path: '/service_type',
    name: 'service_type',
    component: service_type
  },
  {
    path: '/crowsourcing_index',
    name: 'crowsourcing_index',
    component: crowdsourcing_index
  },
//   {
//     path: '/about',
//     name: 'About',
//     component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
//   },
//   {
//     path: '/test',
//     name: 'Test',
//     component: () => import(/* webpackChunkName: "test" */ '../views/Test.vue')
//   }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
