<template>
 <div>
    <p>vue-json-editor</p>
    <vue-json-editor
    :value="json" 
    :show-btns="true"  
    :mode="'code'" 
    lang="zh" 
    @json-change="onJsonChange" 
    @json-save="onJsonSave" 
    @has-error="onError"></vue-json-editor>
  </div>
 </template>

<script>
export default {
    data () {
      return {
        json: {
          msg: 'demo of jsoneditor'
        }
      }
    },
 
   
 
    methods: {
      onJsonChange (value) {
            console.log('value:', value);
        },
      onJsonSave (value) {
            console.log('value:', value);
        },
       onError (value) {
            console.log('value:', value);
        }
    }

};
</script>

<style>
/* jsoneditor右上角默认有一个链接,加css去掉了 */
.jsoneditor-poweredBy {
  display: none;
}
</style>
